@charset 'utf-8';
@import 'function';

.portfoilo {
  > ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    > li {
      position: relative;
      padding: 0;
      overflow: hidden;

      .backBox {
        width: 100%;
        height: 100%;

        img { width: 100%; height: 100%; object-fit: cover; } 
      }

      .btnBox button:not(:first-child) { margin-top: 5px; }

      &:hover .backBox { filter: brightness(30%); }

      .hoverShow {
        width: 90%;
        height: 80%;
        margin: 30px 5%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        // align-items: center;
        position: absolute;
        top: 0px;
        opacity: 0;
        transition: .3s;

        .titleBox {
          display: flex;
          justify-content: center;
          align-items: center;
  
          img { width: 40px; height: 40px; margin-left: 10px; object-fit: contain; }
        }
      }
      
      &:hover .hoverShow { opacity: 1; }
    }
  }
}