@charset 'utf-8';
// import
@import 'keyframe';
@import 'function';

.intro {
  width: 100%;
  height: 100vh;
  position: relative;
}

.intro .txtBox {
  @include sectionPadding;
  color: #f0f8ff;

  h1 { margin-bottom: 20px; }
  h2 { margin: 30px 0 20px 0; }

  .typingText {
    font-size: 55px;
    font-weight: bold;
    color: $orange;

    &::after {
      content: '';
      display: inline-block;
      width: 3px;
      height: 50px;
      margin-left: 5px;
      position: relative;
      top: 5px;
      // steps : 몇 단계를 걸쳐 한 애니메이션을 실행시킬지 정하는 것
      animation: cursor 1s infinite steps(2);
    }
  }
}

.intro video {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
  overflow: hidden;
  object-fit: cover;
  filter: brightness(30%);

  source { overflow: hidden; }
}

