@charset 'utf-8';

// color
$black: #020715; // (2, 7, 21)
$white: #f0f8ff; // (240, 248, 255)
$orange: orange;
 // header 배경 opacity 주기
@mixin whiteOpacity($opacity) { background-color: rgba(240, 248, 255, $opacity); }
@mixin blackOpacity($opacity) { background-color: rgba(2, 7, 21, $opacity); }
// margin and padding
@mixin sectionPadding { padding: 10vh 5vw; }

// responsive
$mobile-max: 767px;
$tablet-min: 768px;
$tablet-max: 1023px;
$pc-min: 1024px;
@mixin mobile { @media screen and (max-width: $mobile-max) { @content; } }
@mixin tablet { @media screen and (min-width: $tablet-min) and (max-width: $tablet-max) { @content; }}
@mixin pc { @media screen and (min-width: $pc-min) { @content; } }