@charset 'utf-8';
@import 'function';

.popup {
  width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 1;
  backdrop-filter: blur(10px);
  scroll-behavior: auto;

  .popupWrap {
    width: 800px;
    height: 600px;
    padding: 0px 30px 30px 30px;
    position: relative;
    border: 1px solid $white;
    border-radius: 10px;
    background-color: $black;
    overflow-y: scroll;

    img { width: 100%; margin: 20px 0; object-fit: contain; }
    
    button {
      width: 30px;
      height: 30px;
      position: sticky;
      border-width: 2px;
      border-radius: 50%;  
    }
    
    .close { top: 30px; left: 100%; }
    .move { top: 30px; left: 90%; }
  }
}