@charset 'utf-8';
@import 'function';

.skill {

  .skillWrap {
    display: flex;
    flex-flow: wrap;
    justify-content: space-evenly;

    .skillBox {
      position: relative;
      transition: 0.2s;
      overflow: hidden;

      &:hover {
        border: 2px solid $orange;
        transform: translate(-5px, -5px);
        z-index: 0;
        box-shadow: 5px 5px 0px $orange;
        // Hover시 반짝이는 애니메이션
        &::before {
          transform: skewX(45deg) translateX(600px);
        }
      }

      // Hover시 반짝이는 애니메이션
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 70px;
        height: 100%;
        background: rgba(255, 255, 255, 0.5);
        transform: skewX(45deg) translateX(-250px);
        transition: 0.5s;
      }
    }
  }
}