@charset 'utf-8';
@import 'function';

// Tablet
@include tablet {
  h1 { font-size: 35px; }
  h2 { font-size: 30px; }
  h3 { font-size: 25px; }
  h4 { font-size: 22px; }
  p, li { font-size: 18px; }
  // section { min-height: 5vh; }
  section > ul > li { 
    width: 300px;
    height: 300px;
    margin: 10px;
    padding: 20px 30px; 
  }

  .intro {
    // height: 100%;
    .txtBox {
      .typingText {
        font-size: 45px !important;
        &::after { height: 45px !important; }
      }
    }
  }
}

// Mobile
@include mobile {
  h1 { font-size: 30px; }
  h2 { font-size: 27px; }
  h3 { font-size: 24px; }
  h4 { font-size: 20px; }
  p, li { font-size: 16px; }
  // section { min-height: 5vh !important; }
  section > ul > li { 
    width: 250px;
    height: 300px;
    margin: 10px;
    padding: 10px 20px; 
  }
  .intro {
    height: 100%;
    .txtBox {
      .typingText {
        font-size: 40px !important;
        &::after { height: 40px !important; }
      }
    }
  }
  .popup .popupWrap .move { top: 65px !important; left: 100% !important; }
}