@charset 'utf-8';
@import 'function'; // 다른 scss파일을 불러오기

header {
  width: 100%;
  padding: 20px 0;
  margin: auto;
  position: fixed;
  @include whiteOpacity(0.1); // mixin으로 만든 변수는 include로 사용
  backdrop-filter: blur(2px);
  z-index: 1;

  ul {
    display: flex;
    justify-content: space-between;
    width: 300px;
    padding: 0;
    margin: auto;
    
    li { text-align: center; cursor: pointer; transition: 0.3s;}
  }
}