@charset 'utf-8';
@import 'function';

// reset
html, body { margin: 0; padding: 0; }
* {
  box-sizing: border-box; text-decoration: none; list-style: none; margin: 0; padding: 0; word-break: keep-all;
  // 스크롤바 커스텀
  &::-webkit-scrollbar { width: 15px; }
  &::-webkit-scrollbar-thumb { background-color: $orange; border-radius: 10px; }
  &::-webkit-scrollbar-track { background-color: $black; }
}
// common
@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&family=Nanum+Gothic+Coding&display=swap');
* { font-family: 'Nanum Gothic', sans-serif; color: $white; scroll-behavior: smooth; }
h1 { font-size: 50px; }
h2 { font-size: 40px; color: $orange; margin-bottom: 5vh; }
h3 { font-size: 25px; color: $orange; margin-bottom: 20px; }
h4 { font-size: 23px; color: $orange; }
p, li { font-size: 18px; line-height: 1.4; }
section > ul > li { 
  border: 2px solid $white; 
  width: 400px;
  height: 300px;
  margin: 10px;
  border-radius: 15px;
  padding: 30px 50px; 
}
section > ul > li > ul > li { text-align: start; }
section > ul > li > ul > li:not(:first-child) { margin-top: 10px; }
button { font-weight: bold; color: $orange; width: 100%; height: 30px; border: 1px solid $orange; border-radius: 10px; background-color: rgba(255, 255, 255, 0); transition: .3s; cursor: pointer; }
button:hover { color: $black; background-color: $orange; }

// custom
// section { max-width: 1600px; }
// section { min-height: 800px;}
section:not(:first-child) { padding-top: 10vh; background-color: $black; }
section > h2 { text-align: center; }
.active { color: $orange; }
.scrollX { overflow : hidden; }
.orange { color: $orange; font-weight: 600; }
.max1600 { max-width: 1600px; margin: auto; }
